import classnames from 'classnames'

import { CharStatus } from '../../lib/statuses'

let userHasInteractedWithLetter = false

type Props = {
  decryptedLetter: string | null
  encryptedLetter: string | null
  status?: CharStatus
  isHighContrast?: boolean
}

export const DragAndDropLetter = ({
  status,
  decryptedLetter = '',
  encryptedLetter = '',
  isHighContrast = false,
}: Props) => {
  const classesLetter = classnames(
    'flex h-8 w-8 lg:h-10 lg:w-10 items-center justify-center rounded-full m-0.5 text-xl lg:text-2xl font-thin cursor-pointer select-none dark:text-white border-solid border-2 dark:border-white',
    {
      'hover:bg-federal-blue-100 hover:text-white active:bg-federal-blue-100 active:text-white border-federal-blue-100 dark:border-federal-blue-500':
        !status || status === 'absent',
      absent: status === 'absent',
      'correct shadowed bg-cyan-500 hover:bg-cyan-600 active:bg-cyan-700 text-white border-cyan-500':
        status === 'correct' && isHighContrast,
      'present shadowed bg-orange-500 hover:bg-orange-600 active:bg-orange-700 text-white border-orange-500':
        status === 'present' && isHighContrast,
      'correct shadowed bg-spring-green hover:bg-spring-green-500 active:bg-spring-green-600 text-white border-spring-green':
        status === 'correct' && !isHighContrast,
      'present shadowed bg-goldenrod hover:bg-goldenrod-300 active:bg-goldenrod-400 text-white border-goldenrod':
        status === 'present' && !isHighContrast,
    }
  )
  const handleDragStart: React.DragEventHandler<HTMLSpanElement> = (event) => {
    if (!userHasInteractedWithLetter) {
      userHasInteractedWithLetter = true
      window.gtag('event', 'unlock_achievement', {
        achievement_id: 'drag_alphabet_letter',
      })
    }
    const input = (event?.target as HTMLSpanElement)?.ariaLabel || ''
    event.dataTransfer.setData('text', input)
  }

  return (
    <span
      aria-label={decryptedLetter || ''}
      draggable={true}
      onDragStart={handleDragStart}
      className={classesLetter}
      title="Drag and drop me to where you want me to go!"
    >
      {decryptedLetter}{' '}
      {encryptedLetter ? (
        <span className="thin" style={{ fontSize: '12px' }}>
          {encryptedLetter}
        </span>
      ) : null}
    </span>
  )
}
