import { MAX_INCORRECT_GUESSES } from '../../constants/settings'
import { Cell } from '../cryptogram/Cell'
import { BaseModal } from './BaseModal'

const isAndroid = /Android/i.test(navigator.userAgent)

type Props = {
  isOpen: boolean
  handleClose: () => void
}

const onClick = (input: string, ariaLabel: string) => {
  if (isAndroid) {
    alert(`Drag your guess for ${ariaLabel} to this position.`)
  } else {
    alert(`Clicking here allows you to type your guess for ${ariaLabel}.`)
  }
}
let firstTutorial = true

export const InfoModal = ({ isOpen, handleClose }: Props) => {
  if (isOpen && firstTutorial) {
    firstTutorial = false
    window.gtag('event', 'tutorial_begin')
  }

  function onCloseClick() {
    window.gtag('event', 'tutorial_complete')
    return handleClose()
  }

  return (
    <BaseModal title="How to play" isOpen={isOpen} handleClose={onCloseClick}>
      <p className="text-sm text-gray-500 dark:text-gray-300">
        Decode the hidden message. Make up to {MAX_INCORRECT_GUESSES} incorrect
        guesses.
      </p>

      <div className="mt-4 flex justify-center">
        <Cell
          onClick={onClick}
          encryptedValue="V"
          decryptedValue="A"
          status="correct"
        />
        <Cell onClick={onClick} encryptedValue="S" decryptedValue="" />
        <Cell
          onClick={onClick}
          encryptedValue="V"
          decryptedValue="A"
          status="correct"
        />
        <Cell
          onClick={onClick}
          encryptedValue="L"
          decryptedValue="U"
          status="absent"
        />
        <Cell
          onClick={onClick}
          encryptedValue="I"
          decryptedValue="T"
          status="present"
        />
        <Cell
          onClick={onClick}
          encryptedValue="V"
          decryptedValue="A"
          status="correct"
        />
      </div>

      <div className="flex justify-center">
        <Cell onClick={onClick} encryptedValue="U" decryptedValue="" />
        <Cell
          onClick={onClick}
          encryptedValue="I"
          decryptedValue="T"
          status="present"
        />
        <Cell onClick={onClick} encryptedValue="D" decryptedValue="" />
        <Cell onClick={onClick} encryptedValue="M" decryptedValue="" />
        <Cell onClick={onClick} encryptedValue="P" decryptedValue="" />
        <Cell
          onClick={onClick}
          encryptedValue="L"
          decryptedValue="U"
          status="absent"
        />
        <Cell onClick={onClick} encryptedValue="M" decryptedValue="" />
        <Cell onClick={onClick} encryptedValue="O" decryptedValue="" />
      </div>

      <p className="mb-2 text-sm text-gray-500 dark:text-gray-300">
        The message is 'AGATHA CHRISTIE'.
      </p>

      <p className="text-sm text-gray-500 dark:text-gray-300">
        The letter 'A' is correct.
      </p>

      <p className="text-sm text-gray-500 dark:text-gray-300">
        The letter 'T' is present but an incorrect guess.
      </p>

      <p className="text-sm text-gray-500 dark:text-gray-300">
        The letter 'U' is not in the message.
      </p>
    </BaseModal>
  )
}
