import classnames from 'classnames'
import { RefObject, createRef } from 'react'

import { getStoredIsHighContrastMode } from '../../lib/localStorage'
import { CharStatus } from '../../lib/statuses'

const isaLetter = (decryptedValue: string) => {
  return /[a-zA-Z]+/.test(decryptedValue)
}

type Props = {
  encryptedValue: string
  decryptedValue?: string
  status?: CharStatus
  isCompleted?: boolean
  position?: number
  onClick: (input: string, ariaLabel: string) => void
}
const isIphone = /iPhone/i.test(navigator.userAgent)
let userHasInteractedWithCell = false
let userHasDroppedALetter = false

export const Cell = ({
  encryptedValue,
  decryptedValue,
  status,
  isCompleted,
  onClick,
}: Props) => {
  const isFilled = decryptedValue && !isCompleted
  const isHighContrast = getStoredIsHighContrastMode()
  const shouldDisplayDecrypted = isaLetter(encryptedValue)

  const fixedWidth =
    'text-2xl lg:text-2xl xl:text-3xl w-8 h-8 flex items-center justify-center mx-0.5 font-thin rounded dark:text-white focus:ring-4 focus:border-yellow-500'
  const classes = classnames(fixedWidth)
  const classesPunctuation = classnames(
    'text-2xl lg:text-2xl xl:text-3xl w-1 h-8 flex items-center justify-center mx-0.5 font-thin rounded dark:text-white'
  )

  const classesDecrypted = classnames(`${fixedWidth} border-solid border-2`, {
    'hover:bg-federal-blue-100 hover:text-white active:bg-federal-blue-100 active:text-white border-federal-blue-100 dark:border-federal-blue-900':
      !status || status === 'absent',
    'border-federal-blue-900 dark:border-federal-blue-100': isHighContrast,
    absent: status === 'absent',
    'correct shadowed bg-cyan-500 hover:bg-cyan-600 active:bg-cyan-700 text-white border-cyan-500':
      status === 'correct' && isHighContrast,
    'present shadowed bg-orange-500 hover:bg-orange-600 active:bg-orange-700 text-white border-orange-500':
      status === 'present' && isHighContrast,
    'correct shadowed bg-spring-green hover:bg-spring-green-400 active:bg-spring-green-700 text-white border-spring-green':
      status === 'correct' && !isHighContrast,
    'present shadowed bg-goldenrod hover:bg-goldenrod-300 active:bg-goldenrod-400 text-white border-goldenrod':
      status === 'present' && !isHighContrast,
    'cell-fill-animation': isFilled,
  })

  const stylesDecrypted = {
    fontFamily: 'Courier New',
    minheight: '1em',
    minwWidth: '1em',
  }

  const stylesPunctuation = {
    fontFamily: 'Courier New',
    minHeight: '1em',
    minWidth: '0.2em',
  }

  const stylesEncrypted = {
    ...stylesDecrypted,
    marginBottom: '0.5em',
  }

  const allowDrop: React.DragEventHandler<HTMLButtonElement> = (event) => {
    event.preventDefault()
  }

  const onDrop: React.DragEventHandler<HTMLButtonElement> = (event) => {
    event.preventDefault()
    var input = event.dataTransfer.getData('text')
    onClick(input, encryptedValue)
    if (!userHasDroppedALetter) {
      userHasDroppedALetter = true
      window.gtag('event', 'unlock_achievement', {
        achievement_id: 'drop_on_cryptogam_cell',
      })
    }
  }

  const cellOnClick: React.MouseEventHandler<HTMLButtonElement> = (event) => {
    if (!userHasInteractedWithCell) {
      userHasInteractedWithCell = true
      window.gtag('event', 'unlock_achievement', {
        achievement_id: 'click_cryptogram_cell',
      })
    }
    const label = (event?.target as HTMLButtonElement)?.ariaLabel || ''

    onClick('', label)
    if (isIphone) {
      event.currentTarget.blur()
      hiddenInputRef?.current?.focus()
    }
  }

  const notTabbable = shouldDisplayDecrypted ? {} : { tabIndex: -1 }
  const hiddenInputRef: RefObject<HTMLInputElement> = createRef()
  return (
    <div className="inline-flex flex-col">
      <button
        aria-label={encryptedValue}
        className={
          shouldDisplayDecrypted ? classesDecrypted : classesPunctuation
        }
        onClick={cellOnClick}
        onDragOver={allowDrop}
        onDrop={onDrop}
        style={shouldDisplayDecrypted ? stylesDecrypted : stylesPunctuation}
        title={`Placeholder for ${encryptedValue}`}
        {...notTabbable}
      >
        {shouldDisplayDecrypted ? decryptedValue : null}
        <input
          ref={hiddenInputRef}
          style={{ position: 'absolute', top: '-9999px' }}
          tabIndex={-1}
          type="text"
        />
      </button>

      <div
        aria-label={encryptedValue}
        style={shouldDisplayDecrypted ? stylesEncrypted : stylesPunctuation}
        className={shouldDisplayDecrypted ? classes : classesPunctuation}
      >
        {encryptedValue}
      </div>
    </div>
  )
}
